.services {
  position: relative;
  padding-bottom: 0;
  margin-bottom: 0;
  background-color: #4e4e4e;
  background-image: url('../img/gallery1.jpg');
  background-position: bottom;
  background-size: cover;
  background-repeat: no-repeat;
  background-attachment: fixed;

  &:after {
    content: '';
    position: absolute;
    top: 0;
    right: 0;
    width: 100%;
    height: 100%;
    opacity: 0.5;
    background-color: black;
    z-index: 1;
  }

  h2 {
    color: white;
  }

  &__container {
    max-width: 100%;
  }

  &__title {
    margin-bottom: 0;
  }

  &__top {
    position: relative;
    display: flex;
    flex-direction: column;
    justify-content: center;
    margin: 0 auto;
    max-width: 800px;
    z-index: 2;
  }

  &__descr {
    margin: 0;
    color: var(--color-light);
  }

  &__price {
    @include flex-all-sb;

    position: relative;
    padding: 80px;
    margin-bottom: 70px;
    top: 85px;
    background-color: var(--color-black);
    overflow: hidden;
    z-index: 2;


    &-item {
      width: 50%;
      padding: 0 15px;

      opacity: 0;
      transform: translateY(50%);
      transition: all .7s ease;

      @include big-tablet {
        width: 100%;
      }
    }

    @include tablet {
      padding: 50px 0;
    }

    @include big-tablet {
      display: block;
    }
  }

  @include tablet {
    background-position: left;
  }
}

.services__price-item.--active {
  transform: translateY(0%);
}

.price {

  &__item {
    @include flex-all-sb;

    width: 100%;
    margin-bottom: 10px;
    border-bottom: 1px dashed #999999;

    @include mobile {
      margin-bottom: 10px;
    }
  }

  &__subtitle {
    font-weight: 700;
    margin: 0;
    margin-right: 10px;
    color: var(--color-light);

    @include mobile {
      font-size: 14px;
    }
  }

  &__dots {
    display: none;
    height: 18px;
    flex-grow: 1;
    border-bottom: 1px dashed #999999;
  }

  &__value {
    font-weight: 700;
    font-size: 16px;
    line-height: 16px;
    letter-spacing: 2.5px;
    text-align: end;
    margin-left: 10px;
    color: var(--color-primary);

    @include mobile {
      font-size: 14px;
    }
  }
}
