.about {
  padding-bottom: 0;

  &__inner {
    @include flex-all-sb;
    margin-bottom: 70px;

    @include mini-desk {
      display: block;
    }
  }

  &__left {
    max-width: 500px;
    margin-right: 80px;

    @include mini-desk {
      max-width: 100%;
    }

    @include tablet {
      margin-right: 0;
      ;
    }
  }

  &__right {
    position: relative;
    max-width: 1210px;
    max-height: 500px;
    overflow: hidden;

    opacity: 0;
    transform: translateY(30%);
    transition: all .5s ease;

    @include mini-desk {
      width: 100%;
    }

    &:after {
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      z-index: 1;
      width: 100%;
      height: 100%;
      display: block;
      opacity: 0;
      background-color: #000;
      transition: opacity .3s;
    }

    &:hover {
      &:after {
        transition: opacity .3s;
        opacity: .5;
      }
    }

    img {
      transform: scale(1.07);
    }

    &-video {
      display: none;
    }

    &-play {
      position: absolute;
      transform: translate(-50%, -50%);
      left: 50%;
      top: 50%;
      width: 92px;
      height: 92px;
      border-radius: 100%;
      background-color: var(--color-primary);
      z-index: 2;
      opacity: 1;
      transition: all .3s;
      cursor: pointer;

      &:hover {
        transition: all .3s;
        background-color: #c9c8c8;
        opacity: 0.8;

      }

      span {
        position: absolute;
        transform: translate(-40%, -40%);
        left: 50%;
        top: 50%;

        svg {
          width: 30px;
          height: 30px;
          fill: var(--color-light);
        }
      }
    }

    /* &::after {
        content: '';
        position: absolute;
        left: 0;
        right: 0;
        top: 0;
        bottom: 0;
        opacity: .5;
        color: black;
      } */

    @include mini-desk {
      max-width: 100%;
    }
  }

  .about__right.--active {
    transform: translateY(0%);
  }

  &__title {
    position: relative;

    &::before {
      content: "";
      position: absolute;
      bottom: -15px;
      left: 0;
      height: 4px;
      width: 80px;
      background-color: #d0a37c;
    }
  }

  &__advent {
    display: none;
    padding: 70px 0;
    background-color: #fafafa;
  }

  &__list {
    display: flex;
    align-items: center;
    justify-content: space-between;

    &-descr {
      letter-spacing: 0.5px;
      padding-top: 20px;
      margin: 0;
    }
  }

  &__item {
    @include flex-column-center;

    text-align: center;
    width: 110px;

    &-svg {
      @include flex-all-center;

      position: relative;
      width: 70px;
      height: 70px;
      border-radius: 100%;
      background-color: #ffe5db;
    }

    svg {
      position: absolute;
      top: 7px;
      right: 30px;
      width: 60px;
      height: 60px;
      fill: #2c2c2c;
    }
  }
}
