.our-services {
  position: relative;
  background-repeat: no-repeat;
  background-color: #f9f4f1;
  background-size: cover;

  &__top {
    position: relative;
    margin: 0 auto;
    max-width: 800px;
    z-index: 5;

    &-title {
      position: relative;
      //color: var(--color-primary);

      /*  &:after {
        content: 'Наши услуги';
        position: absolute;
        top: 0;
        left: -25%;
        color: #fbbaa11f;
        font-style: italic;
        font-size: 91px;
      } */
    }

    &-descr {
      //color: white;
    }
  }

  &__items {
    display: flex;
    justify-content: space-between;
    position: relative;
    overflow: hidden;
    z-index: 5;

    @include mini-desk {
      flex-wrap: wrap;
      justify-content: space-evenly;
      margin-left: 30px;
    }

    @include mobile {
      margin-left: 0;
    }
  }

  &__item {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    margin-bottom: 30px;
    width: 380px;
    background-color: var(--color-light);
    text-align: center;
    //box-shadow: 0 25px 98px 0 rgb(0 0 0 / 10%);
    box-shadow: 0px 10px 60px 0px rgb(0 0 0 / 7%);
    transition: all .3s ease-in-out;
    overflow: hidden;
    outline: 1px solid transparent;
    cursor: pointer;

    opacity: 0;
    transform: translateY(30%);

    &--1 {
      transition: all .5s ease .0s;
    }

    &--2 {
      transition: all .5s ease .2s;
    }

    &--3 {
      transition: all .5s ease .4s;
    }

    &-inner {
      flex-grow: 1;
      padding: 30px;
    }

    &-bottom {
      display: flex;
      flex-direction: column;
      align-items: center;
      padding: 30px;
      padding-top: 0;
    }

    &:hover {
      outline: 1px solid #d0a37c;
      transition: all .3s ease-in-out;

      .our-services__item-images img {
        transform: scale(1.05);
        transition: all .3s ease-in-out;
      }

      .our-services__item-images {
        &::after {
          background-color: rgb(44 44 44 / 0%);
          transition: all .3s ease-in-out;
        }
      }
    }

    &:not(:last-child) {
      margin-right: 30px;
    }

    &-title {
      color: var(--color-black);
      margin: 20px 0;
      font-size: 26px;
      line-height: 1.3;
      opacity: 1;

      @include mini-desk {
        font-size: 20px;
      }
    }

    &-images {
      position: relative;
      height: 250px;
      width: 100%;
      transition: all .3s ease-in-out;
      overflow: hidden;

      &::after {
        content: '';
        position: absolute;
        top: 0;
        bottom: 0;
        right: 0;
        left: 0;
        background-color: rgb(255 229 219 / 20%);
        transition: all .3s ease-in-out;
        z-index: 1;
      }

      img {
        width: 100%;
        height: 100%;
        transition: all .3s ease-in-out;
      }
    }

    &-time {
      margin-bottom: 20px;

      span {
        margin-left: 10px;
        font-weight: 700;
        font-size: 24px;
        color: var(--color-black);
      }
    }

    &-link {
      transition: all .3s ease-in-out;
      width: fit-content;

      &:hover {
        color: var(--color-light);
        border-color: var(--color-primary);
        background-color: var(--color-primary);
        transition: all .3s ease-in-out;
      }
    }

    &-text {
      margin: 0;
      letter-spacing: normal;
      text-align: left;
      color: var(--color-gray);

      @include small-tablet {
        text-align: center;
      }
    }

    @include mini-desk {
      margin-right: 30px;
    }

    @include big-tablet {
      width: 360px;
    }

    @include tablet {
      width: 325px;
    }

    @include mobile {
      margin-right: 0 !important;
    }

    @include small-tablet {
      width: 100%;
    }
  }

  .our-services__item.--active {
    opacity: 1;
    transform: translateY(0%);
  }
}

.container--our-services {
  padding: 120px 0;
  max-width: 800px;
  position: relative;
  z-index: 5;
}
