/* stylelint-disable */

@mixin big-desktop {
  @media (max-width: (1440px)) {
    @content;
  }
}

@mixin mini-desk {
  @media (max-width: (1200px)) {
      @content;
    }
}

@mixin big-tablet {
  @media (max-width: (840px)) {
    @content;
  }
}


@mixin tablet {
  @media (max-width: (1024px)) {
    @content;
  }
}

@mixin small-tablet {
  @media (max-width: (768px)) {
    @content;
  }
}

@mixin mobile {
  @media (max-width: (576px)) {
    @content;
  }
}

@mixin small-mobile {
  @media (max-width: (480px)) {
    @content;
  }
}

