.instagram {
  position: relative;
  @include flex-all-center;
  background-image: url('../img/instagram-bg.jpg');
  background-attachment: fixed;
  background-position: bottom;
  background-repeat: no-repeat;
  background-size: cover;
  background-color: var(--color-black);

  &:after {
    content: '';
    position: absolute;
    top: 0;
    right: 0;
    width: 100%;
    height: 100%;
    opacity: 0.6;
    background-color: black;
    z-index: 1;
  }

  &__inner {
    z-index: 2;
    font-weight: 500;
    line-height: 30px;
    font-size: 18px;
    text-align: center;

    svg {
      margin-bottom: 15px;
      height: 80px;
      width: 80px;
      fill: var(--color-primary);
    }

    &--text {
      margin: 0;
      margin-bottom: 25px;
      font-size: 60px;
      font-weight: normal;
      line-height: 1.21;
      color: var(--color-light);

      opacity: 0;
      transition: opacity 2s ease;

      @include big-tablet {
        font-size: 28px;
        line-height: 1.3;
      }
    }

    &--btn {
      display: inline-block;
      font-style: italic;
      color: var(--color-light);
      cursor: pointer;
      transition: all .3s;

      &:hover {
        color: var(--color-primary);
        background-color: transparent;
        border-color: var(--color-primary);
        transition: all .3s;
      }
    }
  }
}
