.header {
  position: absolute;
  padding-bottom: 10px;
  top: 0;
  right: 0;
  left: 0;
  color: var(--color-light);
  background-color: transparent;
  z-index: 999;

  &__border {
    max-width: 100%;    
  }

  &__top {
    height: 45px;    
    border-bottom: 1px solid rgba(132, 132, 132, 0.15);
    background-color: transparent;

    .top-container {
      @include flex-all-sb;

      height: 100%;
    } 
  }

  &__social {
    @include flex-v-center;

    &-item {
      &:not(:last-child) {
        margin-right: 20px;
      }
    }

    &-link {      
      display: block;
      width: 18px;
      height: 18px; 
      
      &:hover {
        svg {
          fill: var(--color-primary);
          scale: 1.2;
          transform: translateY(-2px);
          transition: all .3s;
        }
      }
      
      svg {
        display: block;
        width: 100%;
        height: 100%;
        object-fit: cover;
        fill: var(--color-light);
        transition: all .3s;
      }
    }    
  }

  &__contacts {
    @include flex-v-center;
    position: relative;

    &-link {
      @include flex-v-center;
      color: var(--color-light);
      font-weight: 400;
      font-size: 14px;
      line-height: 45px;
      transition: all .3s;

      &:hover:not(:last-child) {
        transition: all .3s;
        color: var(--color-primary);

        svg {
          fill: var(--color-primary);
          transition: all .3s;
        }
      }
      
      svg {
        margin-right: 10px;
        height: 15px;
        width: 15px;
        fill: var(--color-light);
        transition: all .3s;
      }

      &:not(:last-child) {
          margin-right: 20px;
        }
    }
  }

  &__bottom {
    @include flex-all-sb;
    
    padding-top: 10px;
    
    &-btn {
      transition: all .3s;
      
      &:hover {
        background-color: var(--color-primary);
        color: var(--color-light);
        transition: all .3s;
      }
    }  
    
  }

  &__logo {
    &-span {
      font-weight: 700;
      font-size: 22px;
      line-height: 36px;
      color: var(--color-light);
      span {
        display: block;
        font-size: 48px;
        width: 100%;
        text-align: center;
      }
    }
  }

  @include tablet {
    display: none;
  }
}

.nav {
  &__list {
    @include flex-v-center;

    @include tablet {
      display: block;
    }
  }

  &__item {      

    &:not(:last-child) {
      margin-right: 40px;
    }

    @include tablet {
      padding-top: 20px;
    }
  }

  &__link {
    font-weight: 700;
    font-size: 12px;
    color: var(--color-light);
    text-transform: uppercase;
    font-size: 13px;
    letter-spacing: 1px;
    transition: all .3s;
    
    &:hover {
      color: var(--color-primary);
      transition: all .3s;
    }  
    
    @include tablet {
      font-size: 18px;
    }
    
  }   
}

.header-tablet {
  position: fixed;
  display: none;
  left: 0;
  top: 0;
  z-index: 500;
  width: 100%;
  transition: transform .4s;

  &__top {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
    background-color: var(--color-black);
    padding: 15px;
  }

  &__bottom {
    position: absolute;
    width: 100%;
    transform: translateX(-150%);
    transition: all .4s;
    background-color: var(--color-black);
    padding: 15px;

    &--active {
      transform: none;
      transition: all .4s;
    }

    &-btn {
      margin-top: 20px;
      width: 100%;
    }
  }

  &__logo {
    &-span {
      font-weight: 700;
      font-size: 22px;
      line-height: 36px;
      color: var(--color-light);

      span {
        display: block;
        font-size: 48px;
        width: 100%;
        text-align: center;
      }
    }
  }

  &__social {
    @include flex-v-center;

    &-item {
      &:not(:last-child) {
        margin-right: 20px;
      }
    }

    &-link {
      display: block;
      width: 18px;
      height: 18px;

      &:hover {
        svg {
          fill: var(--color-primary);
          scale: 1.2;
          transform: translateY(-2px);
          transition: all .3s;
        }
      }

      svg {
        display: block;
        width: 100%;
        height: 100%;
        object-fit: cover;
        fill: var(--color-light);
        transition: all .3s;
      }
    }
  }

  &__nav {
    padding-bottom: 20px;
    border-bottom: 2px solid #e7e7e74a;

    &-list {
      text-align: center;
    }

    &-item {
      padding-top: 20px;
    }

    &-link {
      display: block;
      font-weight: 700;
      font-size: 12px;
      color: var(--color-light);
      text-transform: uppercase;
      font-size: 18px;
      letter-spacing: 1px;
      transition: all .3s;

      &:hover {
        color: var(--color-primary);
        transition: all .3s;
      }


    }
  }

  @include tablet {
    display: block;
  }

}

