.btn-top {
  @include flex-all-center;

  position: fixed;
  bottom: 40px;
  right: 40px;
  height: 58px;
  width: 58px;
  border-radius: 50%;
  background-color: var(--color-primary);
  transition: background-color .3s;
  cursor: pointer;
  z-index: 99;

  svg {
    height: 21px;
    width: 21px;
    fill: var(--color-light);
  }

  &:hover {
    background-color: #c9c8c8;
    transition: background-color .3s;
  }
}