.form-field {
  @include flex-column-start;

  &__caption {
    display: none;
    margin-bottom: 7px;
    font-size: 14px;
    line-height: 150%;
    color: var(--color-gray);
  }

  &__input {
    //border: 1px solid #D7DADD;
    border: none;
    border-radius: 4px;
    padding-left: 15px;
    //background: #d0a37c1a;
    width: 260px;
    height: 44px;
    font-size: 14px;
    line-height: 150%;

    &::placeholder {
      color: #9A9CA5;
    }

    &--big {
      width: 414px;
    }
  }


  &--gray {
    width: 100%;

    .form-field__input {
      width: 100%;
      background: #373737;

      &--big {
        padding-top: 10px;
        height: 85px;
        resize: none;
      }
    }
  }
}